@import "../extends.scss";

.slider {
    user-select: none;
    margin: 0;
    width: 100%;
    height: 280px;
    background-color: $colorBackground__slider;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 15px;

    .left,
    .right {
        width: 50px;
        height: 50px;        
        border: 2px solid $color_inputBorder;
        background: $colorBackground__slider__card;
        color: $color_inputBorder;
        border-radius: 50%;
        display: flex;
        align-items: center;
        cursor: pointer;
        font-size: 30px;
    }
    .left:hover,
    .right:hover {
        border: 2px solid $colorBackground__slider__card;
        background: $color_inputBorder;
        color: $colorBackground__slider__card;
        border-radius: 50%;
        display: flex;
        align-items: center;
        cursor: pointer;
        font-size: 30px;
    }
    .left {
        padding-left: 5px;
        margin-left: 15px;
    }
    .right {
        padding-left: 12px;
        margin-right: 15px;
    }
    .disabled,
    .disabled:hover {
        cursor: default;
        border: 2px solid $colorBackground_textForDark;
        background: $colorBackground__slider__card;
        color: $colorBackground_textForDark;
    }

    .cardList {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        .card {
            cursor: pointer;
            background-color: $colorBackground__slider__card;
            flex-basis: 29%;
            height: 90%;
            margin: 0 15px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            border-radius: 10px;            

            .title {
                text-align: center;
                width: 100%;
                font-size: 16px;
                font-weight: 700;
                overflow:hidden;
                text-overflow: ellipsis;
            }

            .img {
                width: 100%;
                height: 50%;
                background: center / cover no-repeat;
            }

            .description {
                width: 90%;
                overflow: hidden;
                text-align: center;
                font-size: 15px;
            }
        }

        .card:hover {
            flex-basis: 30%;
            height: 95%;
            margin: 0 10px;
            transition: 0.3s;
        }        
    }

    .cut {
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
    }
    
    .cut_title {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
    }
}

.fullscreen {
    .cardList {
        flex-basis: 88%;

        .card {
            cursor: default;
            width: 95%;
            height: 85%;
            flex-basis: auto;
            justify-content: space-between;
            padding: 10px 0;

            .img {
                order: 1;
            }

            .title {
                order: 2;
                font-size: 30px;
            }

            .description {
                order: 3;
                font-size: 20px;
            }
        }

        .card:hover {
            flex-basis: auto;
            height: 85%;
            margin: 0 15px;
        }
    }

    .cut {
        -webkit-line-clamp: 10;
    }
}